function rotate(cell, x, y, z) {
  let mat = new Matrix4();
  mat.rotateX(x);
  mat.rotateY(y);
  mat.rotateZ(z);

  cell.eachVertex(v => {
    let pos = v.pos.slice();
    pos.push(1);
    let posVec = mat.transformVector4(pos);
    v.posVec = posVec;
  });
}

