import {getGraticule, getPolyhedra, getDistanceField} from './projections.js';

onmessage = (e) => {
  const {name, descriptor} = e.data;
  const descName = descriptor.name;
  let ans;

  if (descName === 'GRATICULE' || (~['TETRAHEDRON',
              'CUBE',
              'OCTAHEDRON',
              'DODECAHEDRON',
              'ICOSAHEDRON'].indexOf(descName))) {
    ans = getGraticule(descriptor, e.data.imgData);
    postMessage({
      name,
      steps: ans
    });

  } else if (['TETRAHEDRON',
              'CUBE',
              'OCTAHEDRON',
              'DODECAHEDRON',
              'ICOSAHEDRON'].filter(n => ~descName.indexOf(n)).length) {
    ans = getPolyhedra(descriptor, e.data.imgData);
    postMessage({
      name,
      steps: ans
    });
  } else if ((~descName.indexOf('COASTLINE')) || (~descName.indexOf('TECTONIC'))) {
    fetch(`${name}_small.json`)
      .then(function(response) {
        return response.json()
      }).then(function(json) {
        postMessage({
          name,
          steps: json
        });
      }).catch(function(ex) {
         //parsing failed
      });
    //ans = getDistanceField(descriptor, e.data.imgData);
    //postMessage({
      //name,
      //steps: ans
    //});
    //debugger;
  }

};





